*{margin:0;padding:0;border:0;box-sizing:border-box}body{background-color:#fff;font-size:16px;font-family:"Lato",sans-serif;color:#492b2f}ul,ol{list-style:none}a{color:inherit;text-decoration:none;transition:all 0.1s linear}input{background:rgba(255,255,255,0);color:#492b2f;font-size:1.1rem;font-family:inherit;padding:1rem}input::-webkit-input-placeholder{font-size:1rem;color:rgba(110,58,75,0.9)}input:-ms-input-placeholder{font-size:1rem;color:rgba(110,58,75,0.9)}input::placeholder{font-size:1rem;color:rgba(110,58,75,0.9)}input:focus{outline:none;color:#492b2f}h1,h2{font-family:"Staatliches",sans-serif;text-transform:lowercase}h1{font-size:3rem;letter-spacing:2px}h2{font-weight:100;letter-spacing:1px}

.App{padding-bottom:3rem;min-height:90vh}.App .loading-message{color:rgba(110,58,75,0.9);width:100%;margin:2rem 0;padding:3rem;text-align:center;-webkit-animation:slide 3s linear infinite 0s;animation:slide 3s linear infinite 0s;overflow:hidden;position:fixed}@-webkit-keyframes slide{0%{opacity:0;-webkit-transform:translateX(-2rem);transform:translateX(-2rem)}50%{opacity:1;-webkit-transform:translateX(0);transform:translateX(0)}100%{opacity:0;-webkit-transform:translateX(2rem);transform:translateX(2rem)}}@keyframes slide{0%{opacity:0;-webkit-transform:translateX(-2rem);transform:translateX(-2rem)}50%{opacity:1;-webkit-transform:translateX(0);transform:translateX(0)}100%{opacity:0;-webkit-transform:translateX(2rem);transform:translateX(2rem)}}.App .message{color:rgba(110,58,75,0.9);width:100%;margin:2rem 0;padding:3rem;text-align:center;overflow:hidden;position:fixed}

.topbar{width:85%;margin:2em 0;background-color:#492b2f}.topbar .topbar__link{display:block;width:100%;text-align:right;padding:16px;letter-spacing:0.1em}.topbar .topbar__link:hover,.topbar .topbar__link:focus{box-shadow:0pt 0pt 0 4pt rgba(110,58,75,0.9)}.topbar .topbar__link>*{color:#fff}@media (max-width: 600px){.topbar .topbar__link>*{font-size:2.5em}}

.table-container{display:flex;width:100%;justify-content:center}.table-container .table-container__table{width:90%;border-collapse:collapse}.table-container .table-container__table>.table__head tr{color:#492b2f;border-bottom:0.5rem solid #fff}.table-container .table-container__table>.table__head tr th{padding:2rem;min-width:10%;line-height:1.5;height:auto;cursor:pointer}.table-container .table-container__table>.table__head tr th:hover{color:rgba(110,58,75,0.9)}.table-container .table-container__table>.table__body{letter-spacing:0.5px}.table-container .table-container__table>.table__body .table-body__row{border-bottom:2rem solid #fff;transition:0.7s all ease-in-out;background-color:#fff}.table-container .table-container__table>.table__body .table-body__row:hover{-webkit-transform:scale(1.012);transform:scale(1.012)}.table-container .table-container__table>.table__body .table-body__row.table-body__row--open{background:linear-gradient(90deg, rgba(205,234,196,0.5) 0%, #cdeac4 10%, rgba(205,234,196,0.5) 100%)}.table-container .table-container__table>.table__body .table-body__row.table-body__row--open:hover{background:linear-gradient(90deg, rgba(205,234,196,0.9) 0%, #cdeac4 10%, rgba(205,234,196,0.9) 100%)}.table-container .table-container__table>.table__body .table-body__row.table-body__row--closed{background:linear-gradient(90deg, rgba(244,213,213,0.5) 0%, #f4d5d5 10%, rgba(244,213,213,0.5) 100%)}.table-container .table-container__table>.table__body .table-body__row.table-body__row--closed:hover{background:linear-gradient(90deg, rgba(244,213,213,0.9) 0%, #f4d5d5 10%, rgba(244,213,213,0.9) 100%)}.table-container .table-container__table>.table__body .table-body__row>td{padding:2rem;min-width:10%;line-height:1.5;height:auto;border-left:0.25rem solid #fff;border-right:0.25rem solid #fff}.table-container .table-container__table>.table__body .table-body__row>td.row__td-description{width:45%;cursor:pointer;text-align:justify}.table-container .table-container__table>.table__body .table-body__row>td.row__td-description:hover .expand-button,.table-container .table-container__table>.table__body .table-body__row>td.row__td-description:active .expand-button{font-style:italic}.table-container .table-container__table>.table__body .table-body__row>td.row__td-description .expand-button{color:rgba(110,58,75,0.9);display:block;opacity:0.6;font-size:0.8em;padding-top:1em;text-align:right}.table-container .table-container__table>.table__body .table-body__row>td.row__td-description .expand-button:hover,.table-container .table-container__table>.table__body .table-body__row>td.row__td-description .expand-button:active{opacity:0.8}@media (max-width: 1120px){.table-container .table-container__table{margin-top:1rem;width:70%;display:flex;flex-direction:column}.table-container .table-container__table>.table__head{display:flex;flex-direction:column;align-items:center}.table-container .table-container__table>.table__head tr{display:flex;justify-content:center;text-decoration:overline;flex-wrap:wrap}.table-container .table-container__table>.table__head tr th{padding:0.9em}.table-container .table-container__table>.table__head::before{content:"Sort by:";margin-bottom:1rem;opacity:0.8}.table-container .table-container__table>.table__body tr.table-body__row{display:flex;flex-direction:column;width:100%}.table-container .table-container__table>.table__body tr.table-body__row>td{padding:4vh 8vw;text-align:justify}.table-container .table-container__table>.table__body tr.table-body__row>td:first-child{padding-top:10vh}.table-container .table-container__table>.table__body tr.table-body__row>td:last-child{padding-bottom:10vh}.table-container .table-container__table>.table__body tr.table-body__row>td.row__td-description{width:100%}.table-container .table-container__table>.table__body tr.table-body__row>td::before{content:attr(data-column);display:flex;font-family:"Staatliches";font-size:1.5em;padding-bottom:0.5em;justify-content:flex-start}}@media (max-width: 780px){.table-container .table-container__table{width:100%}}

.searchbar{width:100%;margin:4rem 0 1rem;display:flex;justify-content:center}.searchbar ul{width:25rem;min-width:auto;margin:0 10%;display:flex;justify-content:space-between;align-items:center;border-radius:25px;border:3px solid #492b2f;transition:all 1s}.searchbar ul:focus,.searchbar ul:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.searchbar ul:hover{cursor:text}.searchbar ul .searchbar__icon{font-size:1.5rem;padding:15px;color:#492b2f;cursor:pointer}@media (max-width: 350px){.searchbar{-webkit-transform:scale(0.9);transform:scale(0.9)}}

.pagination-container{width:100%;display:flex;justify-content:center}.pagination-container .pagination-list{display:flex;justify-content:space-between}.pagination-container .pagination-list>li{color:#fff;background-color:rgba(110,58,75,0.9);min-width:3rem;height:3rem;margin:1rem;display:flex;justify-content:center;align-items:center;padding:5px;border-radius:40%}.pagination-container .pagination-list>li:hover{background-color:#492b2f;-webkit-transform:scale(1.1);transform:scale(1.1);cursor:pointer}.pagination-container .pagination-list>li:active{-webkit-transform:scale(1.2);transform:scale(1.2)}.pagination-container .pagination-list>li.pagination-list__arrow--back,.pagination-container .pagination-list>li.pagination-list__arrow--forward{opacity:0.5;font-size:2em;font-weight:bold;min-width:2.1rem;width:2.1rem;height:2.1rem;align-self:center}.pagination-container .pagination-list>li.pagination-list__item--active{background-color:#492b2f;-webkit-transform:scale(1.2);transform:scale(1.2);font-size:1.1em}@media (max-width: 791px){.pagination-container .pagination-list{margin:0.8rem 0}}

